.partners {
  max-width: 1100px;
  width: 100%;
  margin: 0 auto 20vh;
}

.partners ul {
  margin: 0 auto;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  background-color: #202125;
  padding: 20px;
  border-radius: 20px;
}

.partners ul li {
  width: 25%;
}

